.container {
  width: 100%;
  height: 80px;
  padding: 8px 64px;
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10;
  transition: 0.4s ease;
}

.container:hover {
  background-color: #1a237e;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.container a {
  text-decoration: none;
  color: black;
}

.container ul {
  padding: 0;
  list-style: none;
}

.Logo {
  width: 160px;
}

.Nav__option__list {
  display: flex;
}

.Link {
  font-weight: 900;
  font-size: 16px;
  margin: 4px 18px;
  transition: all 300ms ease-out;
}

.Link:hover {
  transform: translateY(-5px);
}

.icon__list {
  display: flex;
}

.icon {
  font-size: 24px;
  padding: 4px 8px;
}
.icon:hover {
  transform: scale(1.1);
}

.phoneCall {
  padding: 0 12px;
  height: 60px;
  font-size: var(--font-large);
  color: var(--color-white);
  background-color: var(--color-blue);
  border-radius: var(--border-radius);
}

.phoneCall:hover {
  background-color: var(--color-dark-blue);
  cursor: pointer;
}

.phoneCall a {
  color: var(--color-white);
  margin-left: 8px;
}
